@import url(https://fonts.googleapis.com/css2?family=Open+Sans&family=Oswald:wght@500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@300;400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1,h2,h3{font-family:'Josefin Sans', sans-serif}h3{text-transform:uppercase;letter-spacing:.6rem;font-size:1rem}.header{width:100%;display:flex;align-items:center;height:100px}.header a{margin:0 28px;text-decoration:none;color:#181818;font-size:12px;font-weight:600;font-family:'Raleway', sans-serif}.container{position:relative;color:#181818;width:1280px;min-width:1280px;margin:0 auto;display:flex;justify-content:center;color:white}.container .page{position:absolute}.container .page .inner{display:flex;justify-content:center;height:75vh}.container .page .inner h1{font-size:61px;font-weight:800;letter-spacing:0.08rem}.container .page .inner h1 .line-wrap{overflow:hidden;height:86px}.container .page .inner p{font-family:'Raleway', sans-serif;margin-top:200px;width:340px;font-weight:300;line-height:27px;font-size:16px}.container .page .inner button{margin-top:20px;width:170px;height:50px;background:none;font-family:'Roboto Mono', monospace;font-size:14px;cursor:pointer}.container .page .inner button:hover{background-color:white;color:#1b1b1c;height:55px}.container .page .inner .black{background-color:#1b1b1c;color:white}.page-enter{opacity:0}.page-enter-active{opacity:1;transition:opacity 400ms;transition-delay:600ms}.page-exit{opacity:1}.page-exit-active{opacity:0;transition:opacity 400ms}.inner{background-size:cover;background-position:center;padding:40px;box-shadow:inset 0 0 0 100vw rgba(0,0,0,0.3);min-width:90vw}.about{background-image:url(/home/static/media/3.02aadce3.jpg)}.home{background-image:url(/home/static/media/4.8f9119cb.jpg)}.contact{background-image:url(/home/static/media/2.5d8cd3ba.jpg)}.grid-container{display:flex;justify-content:center;height:100vh;width:90vw;display:grid;display:grid;margin-bottom:5vh;grid-template-columns:1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;grid-template-rows:1fr 1fr 1fr;grid-template-areas:"main main main main main main side side side side" "main main main main main main side side side side" "bottom1 bottom1 bottom1 bootom2 bootom2 bootom2 bootom2 bottom3 bottom3 bottom3"}.bottom1{grid-area:bottom1;box-shadow:inset 0 0 0 100vw rgba(226,196,165,0.8);background-image:url(/home/static/media/7.77dd1596.jpg);background-size:cover;background-repeat:no-repeat}.bootom2{grid-area:bootom2;padding:3rem;color:black;line-height:2rem}.bottom3{grid-area:bottom3;background-image:url(/home/static/media/5.05df8909.jpg);background-size:cover;background-repeat:no-repeat;box-shadow:inset 0 0 0 100vw rgba(0,0,0,0.7)}.side{grid-area:side;padding:3rem;color:black;line-height:2rem}.main{grid-area:main;background-image:url(/home/static/media/1.f44df889.jpg);background-size:cover;background-repeat:no-repeat}

